@import "reset.css";
@import "fonts.css";

body, html {
  height: 100%;
}

body {
  font-family: "Helvetica", serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #5C5C5E;

}

.wrapper {
  min-height: 100%;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

main {
  flex: 1 1 auto;
}

.container {
  max-width: 1220px;
  padding: 0 10px;
  margin: 0 auto;
}

.header {
  background: #F6F4F5;
  border-bottom: 1px solid #CACAD6;
}

.nav {
  height: 80px;
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.menu {
  display: flex;
  gap: 0 20px;
}

.logo {
  padding-right: 262px;
}

.top {
  background-image: url("../images/Rectangle 2.png");
  background-repeat: no-repeat;
  background-size: cover;
  height: calc(100vh - 80px);
  margin-bottom: 100px;

}

.top__inner {

}

.top__title {
  font-size: 96px;
  line-height: 110px;
  color: #5C5C5E;
  padding-top: 230px;
  margin-bottom: 10px;
}

.top__subtitle {
  margin-bottom: 5px;
  font-size: 36px;
  line-height: 41px;
}

.top__text {
  margin-bottom: 50px;
  font-size: 13px;
  line-height: 15px;
}

.top__button-sign {
  font-weight: 400;
  font-size: 13px;
  line-height: 15px;
}

.top__button {
  cursor: pointer;
  border: none;
  background: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%);

  display: flex;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  margin-bottom: 10px;
  padding: 1px;
}
.top__button-text{
  color: #F6F4F5;
  padding: 20px 110px;
}
.top__button-price{
  padding: 20px 105px;
  background-color: #E6E4E5;
  color: #5C5C5E;
}

.box__inner{
  display: grid;
  grid-template-columns:580px 280px 300px ;
  grid-template-rows: 248px 248px;
  margin-bottom: 100px;
  gap: 32px 20px;
}
.box__item-title{
  font-size: 36px;
  line-height: 41px;
  text-transform: uppercase;
  background: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  text-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.box__title{
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #5C5C5E;
  margin-bottom: 50px;
  padding-top: 100px;
}
.box__item img{
  margin-bottom: 10px;
}
.box__item-one{
  grid-row: 1/2;
}
.box__item-five{
 grid-column: 3/3;
  grid-row: 2/2;
  margin-left: 20px;
}
.box__item-four{
  grid-column: 2/2;
  grid-row: 2/2;
}
.box__item-three{
  margin-left: 20px;
}

.product__title{
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  color: #5C5C5E;

}
.swiper-slide{

}
.swiper {

  width: 900px;
  height: 400px;
}
.product__buttons{
  padding-top: 84px;
  display: flex;
  flex-direction: column;
  max-width: 400px;
  align-items: start;
}
._active{
  color: #070707 !important;
}
.product__inner{
 display: flex;
  gap: 115px;
}
.product__button{
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #5C5C5E;
  margin-bottom: 20px;
}
.slide__inner{
  display: flex;
  gap: 20px;
}
.slide__title{
  font-weight: 400;
  font-size: 36px;
  line-height: 41px;
  text-transform: uppercase;
  background: linear-gradient(180deg, #BC10D8 0%, rgba(80, 0, 250, 0.93) 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 20px;
}
.slide__img{
  padding-right: 20px;
  border-right: 1px solid #CACAD6;
}
.slide__subtitle{
  padding-top: 102px;
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 10px;
}
.slide__text{
  margin-bottom: 44px;
  max-width: 274px;
}
